import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { fetchItems } from '../redux/actions.ts';
import { RootState } from '../redux/rootReducer.ts';

import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import List from '@mui/material/List';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import { Typography } from '@mui/material';
import 'react-quill/dist/quill.snow.css';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import useScrollTrigger from '@mui/material/useScrollTrigger';
import Fab from '@mui/material/Fab';
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';
import Fade from '@mui/material/Fade';

interface Props {
  window?: () => Window;
  children?: React.ReactElement<unknown>;
}

function ScrollTop(props: Props) {
  const { children, window } = props;
  const trigger = useScrollTrigger({
    target: window ? window() : undefined,
    disableHysteresis: true,
    threshold: 100,
  });

  const handleClick = (event: React.MouseEvent<HTMLDivElement>) => {
    const anchor = (
      (event.target as HTMLDivElement).ownerDocument || document
    ).querySelector('#back-to-top-anchor');

    if (anchor) {
      anchor.scrollIntoView({
        block: 'center',
      });
    }
  };

  return (
    <Fade in={trigger}>
      <Box
        onClick={handleClick}
        role="presentation"
        sx={{ position: 'fixed', bottom: 16, right: 16 }}
      >
        {children}
      </Box>
    </Fade>
  );
}

const label = { inputProps: { 'aria-label': 'Checkbox demo' } };

const ItemList: React.FC = (props: Props) => {
  const dispatch = useDispatch();
  const [query, setQuery] = useState("");
  
  useEffect(() => {
    dispatch(fetchItems());
  }, [dispatch]);

  const { items, loading, error } = useSelector((state: RootState) => state.item);
  const [result, setResult] = useState(items);

  useEffect(() => {
    setResult(items);
  }, [items]);

  useEffect(() => {
    setResult(items.filter((quiz) => filterFunction(quiz.question.toLowerCase(), query.toLowerCase().trim())));
  }, [query]);

  const filterFunction = (question, searchTerm) => {
    var tags = searchTerm.split(/\s+/);
    var cnt = 0;
    tags.map(tag => {
      if (question.includes(tag)) cnt++;
    });
    return cnt == tags.length;
  };

  if (loading) return <div>Loading...</div>;
  if (error) return <div>Error: {error}</div>;

  return (
    <div style={{ minWidth: '400px' }}> {/* Ensure the container has a min-width of 400px */}
      <AppBar>
        <Toolbar sx={{ display: 'flex', flexDirection: 'column', alignItems: 'stretch', padding: '10px' }}>
          <Box sx={{ display: 'flex', width: '100%', justifyContent: 'space-between', alignItems: 'center' }}>
            <TextField
              id="outlined-basic"
              label="Search what you want"
              color="error"
              variant="outlined"
              sx={{ flexGrow: 1, marginRight: '10px', fontSize: '20px' }}
              onChange={(e) => setQuery(e.target.value)}
            />
          </Box>
          <Typography variant="body2" sx={{ marginTop: '5px', textAlign: 'left' }}>
            {result.length} results
          </Typography>
        </Toolbar>
      </AppBar>

      <Toolbar id="back-to-top-anchor" />
      <List sx={{ minWidth: '20%', bgcolor: 'background.paper', fontSize: '23px', fontFamily: 'Segoe UI', marginTop: "30px" }} component="nav">
        {result.sort((a: any, b: any) => b.checked - a.checked).map((quiz, index) => (
          <div key={quiz._id}>
            <ListItemButton sx={{ padding: "0px" }}>
              <ListItemText>
                <b style={{ fontSize: '30px', fontFamily: 'Segoe UI' }}>{index + 1}. {quiz.question}</b>
              </ListItemText>
            </ListItemButton>

            <List component="div" disablePadding>
              <ListItemButton sx={{ pl: 4, pt: 0, pb: 0 }}>
                <div dangerouslySetInnerHTML={{ __html: quiz.answer.replace(/<p>/g, '<p style="margin:0">') }} style={{ padding: "0px" }} />
              </ListItemButton>
            </List>
          </div>
        ))}
      </List>

      <ScrollTop {...props}>
        <Fab size="small" aria-label="scroll back to top">
          <KeyboardArrowUpIcon />
        </Fab>
      </ScrollTop>
    </div>
  );
};

export default ItemList;
